* {
  font-family: Arial, Helvetica, sans-serif;
}

body {
  padding: 60px;
  font-size: 1.5rem;
}
input {
  font-size: 1rem;
  width: 40vw;
  color: gray;
}

.card-grid {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;

}
.card {
  align-content: center;
  border: 1px solid #2a2a2a;
  border-radius: 6px;
  -webkit-border-radius: 6px;
  -moz-border-radius: 6px;
  -ms-border-radius: 6px;
  -o-border-radius: 6px;
  margin-bottom: 10px;
  margin-right: 10px;
  
  overflow: hidden;
  justify-content:space-between
}
.card p {
  padding: 5px;
  text-align: center;
  font-size: 16px;;
}
.card img {
  height: 150px;
  width: 100%;
}
